* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  color: white;
  font-family: 'Open Sans', sans-serif;
  background-color: #131211;
}

.wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  width: 320px;
  min-height: 450px;
  background-color: #3f3a38;
}

header {
  img {
    max-width: 50px;
    display: block;
    margin: 0 auto;
    padding: 20px 0;
  }
  h1 {
    text-align: center;
    margin: 0;
    font-weight: lighter;
    font-size: 20px;
    letter-spacing: 5px;
  }
  .counter-container {
    overflow: hidden;
    margin: 0 auto;
    width: 50%;
    padding: 10px;
  }
  .counter {
    display: inline-block;
  }
}

section {
  min-height: 240px;
  .element {
    height: 40px;
    width: 100%;
    background-color: #f1896a;
    display: flex;
    align-items: center;
    padding-left: 25px;
    border-bottom: 1px solid #3f3a38;
  }
  .element:last-of-type {
    border-bottom: none;
  }
  .complete {
    background-color: #eb676d;
    background-image: url(../img/check.png);
    background-position: 95%;
    background-size: 20px;
    background-repeat: no-repeat;
    padding-right: 50px;
  }
}

footer {
  overflow: hidden;
  padding: 25px 0;
}

form {
  margin: 0 auto;
  position: relative;
  input, button {
    color: #3f3e42;
    box-shadow: none;
    border-radius: 5px;
    border: none;
    padding: 4px 10px;
  }
  input {
    width: 240px;
    display: block;
    margin: 0 auto;
    background-color: #9c9b9a;
  }
  input::-webkit-input-placeholder {
  color: #3f3e42 !important;
  }
  button {
    color: #ffffff;
    position: absolute;
    top: 20px;
    right: -20px;
  }
}

button {
  border-radius: 5px;
  border: none;
  padding: 4px 10px;
  color: #ffffff;
  background-color: #eb676d;
  float: right;
  margin-right: 30px;
}

@media screen and (max-width: 480px) {
  .wrapper {
    width: 100% !important;
    height: 100vh;
  }
  section {
    height: 50vh;
    padding-top: 50px;
  }
}
